.wearables {
  margin-bottom: 174px;
  @include sm {
    text-align: center;
    margin-bottom: 144px; }
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    min-height: 447px;
    background-color: $white;
    @include sm {
      min-height: inherit; } }
  &__images {
    position: absolute !important;
    right: 0;
    left: 39.9%;
    height: 100%;
    min-height: 400px;
    max-width: 64.1%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include sm {
      position: relative !important;
      left: 0;
      max-width: inherit;
      min-height: inherit; } }
  &__image {
    // padding: 30px 120px 30px 150px
    padding: 30px;
    // padding: 30px 160px 30px 150px
    @include coverdiv;
    display: flex;
    align-items: center;
    justify-content: center;
    @include sm {
      padding: 15px; }
    img {
      display: block;
      max-width: 100%;
      max-height: 390px;
      object-fit: cover;
      @include sm {
        max-height: 400px; } } }
  &__texts,
  &__tabs,
  &__info-wrapper {
    width: 35.9%;
    @include sm {
      width: 100%; } }
  &__texts {
    display: flex;
    margin-bottom: 8px; }
  &__text {
    padding: 40px 48px 10px 33px;
    @include sm {
      padding: 28px 20px 10px; }
    .title {
      margin-bottom: 7px; } }
  &__tabs {
    padding-left: 33px;
    padding-bottom: 12px;
    padding-top: 12px;
    margin-bottom: auto;
    @include sm {
      padding-left: 20px; } }
  &__info-wrapper {
    background-image: $grad;
    @include hover {
      .battery__line {
        &:after {
          transform: translateY(0); } } }
    @include sm {
      max-height: 83px; } }
  &__info {
    padding: 44px 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include sm {
      padding: 30px 20px; }
    span {
      color: $white;
      @include bigText;
      display: inline-block;
      vertical-align: middle; }
    .icon {
      font-size: 23px;
      color: $white;
      display: inline-block;
      vertical-align: middle; } } }

.battery {
  position: relative;
  @include hover {
    .battery__line {
      &:after {
        transform: translateY(0); } } }
  &__line {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 140%;
    width: 2px;
    overflow: hidden;
    transform: translate(-50%, -50%) rotate(46deg);
    transform-origin: center;
    &:after {
      content: '';
      @include coverdiv;
      background-color: $white;
      transform: translateY(-101%);
      transition: transform 0.4s; } } }
