.prepaid {
  margin-bottom: 106px;
  &__wrapper {
    background-color: $white;
    text-align: center;
    padding-top: 204px;
    padding-bottom: 60px;
    @include sm {
      padding: 186px 24px 20px; } }

  &__cards {
    text-align: center;
    position: relative;
    top: -271px;
    @include sm {
      top: -238px; } }
  &__tabs {
    margin-top: 22px; } }

.parallax-card {
  display: inline-block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transition: transform 1s ease-out;
  @for $i from 1 through 4 {
    &:nth-child(#{$i}) {
      top: ($i - 1) * 32 + px;
      z-index: 10 - $i;
      @include sm {
        top: ($i - 1) * 26 + px; } } }
  .is-animate & {
    @for $i from 1 through 4 {
      &:nth-child(#{$i}) {
        transform: translate(-50%, -1 * (4 - $i) * 34 + px);
        @include sm {
          transform: translate(-50%, -1 * (4 - $i) * 20 + px); } } } }
  img {
    max-width: 343px;
    display: inline-block;
    filter: drop-shadow(0 20px 20px rgba($black, 0.6));
    @include sm {
      max-width: 240px; } } }
