.header {
  background-color: $white;
  padding: 12px 0;
  position: relative;
  @include sm {
    padding: 14px 0; }
  &__wrapper {
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include sm {
      justify-content: center; } }
  &__nav {
    @include sm {
      position: absolute;
      z-index: 20;
      top: 0;
      left: 0;
      min-height: 100vh;
      padding: 147px 130px 40px 66px;
      transform: translateX(-101%);
      background-color: $white;
      transition: transform $tr, box-shadow $tr;
      .is-open & {
        transform: translateX(0); } } }
  &__burger {
    display: none;
    position: relative;
    z-index: 30;
    @include sm {
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 15px; } } }

.main-nav {
  &__list {
    display: flex;
    align-items: center;
    margin: 0 -30px;
    @include sm {
      flex-direction: column; } }
  &__item {
    padding: 0 30px; }
  &__link {
    color: $mainTextColor;
    font-size: 0;
    display: block;
    @include hover {
      span:after {
        // transform: translateX(0)
        transform: scaleX(1);
        transform-origin: 0 0; } }
    span {
      font-size: 10px;
      line-height: 1.2;
      display: inline-block;
      position: relative;
      overflow: hidden;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 1px;
        width: 100%;
        background-color: $mainTextColor;
        // transform: translateX(-100%)
        transform: scaleX(0);
        transition: transform .4s;
        transform-origin: 100% 0; } } } }

.logo {
  @include flex-center;
  &__text {
    margin-left: 10px;
    color: $mainTextColor;
    font-family: $montserrat;
    font-size: 15px;
    font-weight: 600;
    line-height: 1.533;
    letter-spacing: -0.04em;
    .logo_white & {
      color: $white; }
    .logo_big & {
      font-size: 23px;
      margin-left: 18px; } }
  &__image {
    display: block;
    max-width: 100%;
    .logo_big & {
      height: 35px; } } }

.burger {
  width: 16px;
  height: 12px;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    opacity: 1;
    left: 0;
    background-color: $mainTextColor;
    transform: rotate(0deg);
    transition: transform .15s ease-in-out;
    &:nth-child(1) {
      top: 0px; }
    &:nth-child(2),
    &:nth-child(3) {
      top: 5px; }
    &:nth-child(4) {
      top: 10px; } }
  .is-open & {
    span {
      &:nth-child(1) {
        top: 18px;
        width: 0%;
        left: 50%; }
      &:nth-child(2) {
        transform: rotate(45deg); }
      &:nth-child(3) {
        transform: rotate(-45deg); }
      &:nth-child(4) {
        top: 18px;
        width: 0%;
        left: 50%; } } } }
