.hero {
  @include sm {
    margin-bottom: 20px; }
  &__wrapper {
    margin-bottom: 10px;
    background-color: $white;
    position: relative;
    display: flex;
    align-items: stretch;
    @include sm {
      flex-direction: column;
      background-color: transparent;
 } }      // align-items: center
  &__left {
    @include flex-size(42.7%);
    position: relative;
    z-index: 5;
    @include sm {
      flex: 0 0 auto;
      margin: -40px auto 0;
      min-width: inherit;
      width: 100%;
      max-width: 220px;
      max-height: 270px; } }
  &__right {
    @include flex-size(57.3%);
    @include sm {
      order: -1; } }
  &__right-bg {
    @include cover;
    padding-bottom: 69.8%;
    @include sm {
      padding-bottom: 63.43%; } }
  &__left-bg {
    height: 100%;
    width: 100%;
    background-size: 60%;
    background-position: 64px 44px;
    background-repeat: no-repeat;
    @include sm {
      padding-bottom: 90%;
      background-color: $white;
      background-position: center; } }
  &__center {
    position: absolute;
    bottom: 0;
    left: 213px;
    background-color: $white;
    width: 427px;
    padding: 51px 30px 39px 85px;
    z-index: 1;
    @include md {
      padding: 24px 20px 16px 60px; }
    @include sm {
      position: static;
      padding: 150px 20px 20px;
      text-align: center;
      width: 100%;
      margin-top: -140px; } }
  &__title,
  &__text {
    opacity: 0;
    transform: translate3d(0, -20px, 0); }
  &__title {
    margin-bottom: 8px;
    color: $mainTextColor;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.3;
    @include sm {
      font-size: 15px;
      padding: 0 40px; }
    .is-loaded & {
      animation: fadeInDown 0.9s forwards ease-out; } }
  &__text {
    .is-loaded & {
      animation: fadeInDown 0.9s 0.4s forwards ease-out; } } }

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -10px, 0); }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

